@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
@import url(https://fonts.googleapis.com/css?family=Raleway:500);
*{box-sizing:border-box;font-family:"Raleway"}body{background:#ffecd9;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;color:#62680e;letter-spacing:1px}main{display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1;-webkit-flex-wrap:wrap;flex-wrap:wrap;max-width:600px}.choices{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}h1{padding-top:20px;font-size:70px;text-align:center;letter-spacing:10px}h2{text-align:center}p{font-size:20px;text-align:center}select,button{margin-top:15px;font-size:20px}select{border-radius:4rem;text-align-last:center;width:250px}button{-webkit-appearance:none;background:-webkit-gradient(to right, #a2ccb6 0%, #fceeb5 50%, #ee786e 100%);background:linear-gradient(to right, #a2ccb6 0%, #fceeb5 50%, #ee786e 100%);background-size:500%;border:none;border-radius:5rem;box-shadow:0 .5rem 1rem rgba(0,0,0,.15);color:#fff;cursor:pointer;font:1.5em Raleway,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:3rem;letter-spacing:.05em;outline:none;-webkit-tap-highlight-color:transparent;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:7rem}strong{margin-top:15px;font-size:20px}.HolidayCard{display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1;-webkit-flex-wrap:wrap;flex-wrap:wrap;max-width:800px;-webkit-flex-direction:column;flex-direction:column}.holiday-view{margin-top:50px;background:#e7c5a2;border-style:solid #fff;border-radius:1rem}h5{font-size:16px;margin-left:20px}h4{padding-bottom:15px;margin-left:20px}h3{margin-left:20px;padding-bottom:10px}p{font-size:25px}
